body {
  font-family: "Roboto", sans-serif;
  background-color: #e1e8e9;
}

.todo-input-wrapper {
  position: relative;
  border-bottom: 1px solid #ededed;
}

.todo-input {
  width: 100%;
  font-family: 'Indie Flower', cursive;
  padding: 16px 16px 16px 60px;
   width: 100%;
  box-sizing: border-box;
  font-size: 28px;
  font-style: italic;
  border:solid  rgb(221, 175, 175);
  border-radius: 5px;
  box-shadow: inset 0 -2px 40px rgba(119, 134, 175, 0.15);
}

.fa-plus {
  position: absolute;
  font-size: 24px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  left: 20px;
}

.todo-input::-webkit-input-placeholder {
  font-family: 'Indie Flower', cursive;
}
.todo-input::-moz-placeholder {
  font-family: 'Indie Flower', cursive;
}
.todo-input:-moz-placeholder {
  font-family: 'Indie Flower', cursive;
}
.todo-input:-ms-input-placeholder {
  font-family: 'Indie Flower', cursive;
}

.todo-item {
  font-size: 24px;
  border-bottom: 1px solid #b8b3b3;
  min-height: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  list-style: none;
  padding: 0 16px 0 60px;
  cursor: pointer;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: relative;
}

.text {
  font-family: 'Indie Flower', cursive;
}

.completed {
  text-decoration: line-through;
}

.completed,
.fa-check-circle {
  color: #ccc;
}

.fa-times {
  transition: all .3s ease;
  color: rgba(175, 47, 47, 0.15);
}

.mark {
  position: absolute;
  left: 20px;
}

.todo-item:hover .fa-times {
  color: rgba(175, 47, 47, 0.9);
}

.todo-list {
  margin: 0;
  padding: 0;
}

.footer {
  /* position: relative; */
  min-height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
      padding: 0 6px 0 21px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.amount {
  font-size: 16px;
  font-family: 'Permanent Marker', cursive;
}

@media(max-width:320px){
  .amount{
font-size: 14px;}

}

.btn-group {
  font-family: 'Permanent Marker', cursive;
}

.filter-btn {
  margin: 3px;
  padding: 3px 3px;
  border: 1px solid transparent;
  border-radius: 3px;
  background-color: transparent;
  cursor: pointer;
}

.filter-btn:hover,
.filter-btn.active {
  border-width: 2px;
  border-radius:3px;
  border-color: rgba(175, 47, 47, 0.4);
}

.footer:before {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 50px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2),
                0 8px 0 -3px #f6f6f6,
                0 9px 1px -3px rgba(0, 0, 0, 0.2),
                0 16px 0 -6px #f6f6f6,
                0 17px 2px -6px rgba(0, 0, 0, 0.2);
}

.todo-wrapper {
  max-width: 600px;
  margin: 0 auto;
  position: relative;
  background-color: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}
@media (max-width: 600px) {
  .todo-wrapper {
    margin-left: 10px;
    margin-right: 10px;
  }
}

.title {
  font-family: "Permanent Marker", cursive;
  font-size: 80px;
  text-align: center;
  font-weight: 900;
  color: rgba(175, 47, 47, 0.35);
  margin: 20px auto;
}

@media (max-width: 425px) {
  .title {
    font-size: 60px;
  }
}
@media (max-width: 320px) {
  .title {
    font-size: 50px;
  }
}

